import React from 'react';
import {
  HashRouter as Router,
  Switch,
  Route,
  BrowserRouter,

} from "react-router-dom";

import Certifications from './pages/Certifications';
import Contact from './pages/Contact';
import Education from './pages/Education';
import Experience from './pages/Experience';
import HeadlineProject_Otium from './pages/HeadlineProject_Otium';
import Home from './pages/Home';
import Knowledges from './pages/Knowledges';
import NotFound from './pages/NotFound';
import Portfolio from './pages/Portfolio';
import Publications from './pages/Publications';
import Viz_Domain_Of_Interest from './pages/Viz_Domain_Of_Interest';
import Vizu_Projects_Skills_Identifier from './pages/Vizu_Projects_Skills_Identifier';
import TitrePro from './pages/titrePro.js';

import ContactFooter from './components/ContactFooter';
import NavMenu from './components/NavMenu';
import Identity from './components/Identity';
import Navigations from './components/Navigations';
import CertificationsMenu from './components/cv/CertificationsMenu';

const App = () => {
  return (
    <BrowserRouter>
      <Navigations/>
      <Identity/>
      {/* <NavMenu/> */}
    
      <Router>
        <Switch>
          <Route path="/"  component={Home}/>
          <Route path="/contact" exact component={Contact}/>
          {/* <Route path="certifications" element={<CertificationsMenu key={window.location.pathname}/>}/>
          <Route path="/education" exact component={Education}/>
          <Route path="/experience" exact component={Experience}/>
          <Route path="/otium" exact component={HeadlineProject_Otium}/>
          <Route path="/knowledge" exact component={Knowledges}/>
          <Route path="/portfolio" exact component={Portfolio}/>
          <Route path="/publications" exact component={Publications}/>
          <Route path="/domains" exact component={Viz_Domain_Of_Interest}/>
          <Route path="/skills" exact component={Vizu_Projects_Skills_Identifier}/>
          <Route path="/contact" exact component={Contact}/>
          <Route path="/TitreProfessionnelDWWM" exact component={TitrePro}/> */}
          <Route component={NotFound}/>
        </Switch>
    </Router>

    <footer>
        <ContactFooter/>
    </footer> 
    
    </BrowserRouter>




  );
};


export default App;