
import { NavLink } from 'react-router-dom';
import React from 'react';



const NavMenu = () => {
    return (

        <div id="NavigationMenu">
            <div className= "id-NavMenu">

                <NavLink id='all' to="/" activeClassName="navActive">
                    <a href="cv" className="nav-Item"><span>ALL</span></a>
                    
                </NavLink>

                <NavLink to="experience" activeClassName="navActive">
                <a href="experience" className="nav-Item"><span>Experience</span></a>
                </NavLink>


                <NavLink  to="technologies" activeClassName="navActive">
                <a href="technologies" className="nav-Item"><span>Technologies</span></a>
                </NavLink>

                <NavLink  to="education" activeClassName="navActive">
                <a href="education" className="nav-Item"><span>Education</span></a>
                </NavLink>

                <NavLink  to="certifications" activeClassName="navActive">
                <a href="certifications" className="nav-Item"><span>Certifications</span></a>
                </NavLink>




         
        
            </div>
        </div>
    );
};

export default NavMenu;