
import { NavLink } from 'react-router-dom';
import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
    },
  }));



const Navigations = () => {


    const scrollDown = () => {
      var element = document.getElementsByClassName("contact-footer");

      window.scroll({
        top: 35000,

        behavior: 'smooth'
      });
    }


    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);
  

    // return focus to the button when we transitioned from !open -> open
    const prevOpen = React.useRef(open);
    React.useEffect(() => {
      if (prevOpen.current === true && open === false) {
        anchorRef.current.focus();
      }
  
      prevOpen.current = open;
    }, [open]);
  
    return (

   
    <div className={classes.root}>

      <AppBar position="fixed" color="secondary" id='AppBar'>
        <Toolbar>
          <Typography variant="h6" className={classes.title}>
          </Typography>
          {/* <Button color="inherit">
                <NavLink hidden exact to="/portfolio" activeClassName="navActive">
                    <i className="fas fa-eye"/>
                    <span>Portfolio</span>
                </NavLink>
            
          </Button>
          ééé
          <Button color="inherit">
                <NavLink hidden  exact to="/TitreProfessionnelDWWM" activeClassName="navActive">
                    <i className="fas fa-eye"/>
                    <span>Titre Professionnel</span>
                </NavLink>
            
          </Button> */}
          
          <Button color="inherit" onClick={scrollDown} className="contact">
          <NavLink to="contact" activeClassName="navActive">
            <i className="fas fa-smile-wink"/>
            <span className="conactLink">Contact</span>
          </NavLink>

          
          </Button>
        </Toolbar>
      </AppBar>
    </div>
    


    ); 
};

export default Navigations;